@import "/src/var";

.wrapper {
  max-width: 1440px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  margin-top: 80px;
}
.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1430px;
  width: 100%;
  background-color: $main-color;
  border-radius: 10px;
  color: $white-color;
  height: 100%;
  margin: 0 auto;
}
.title {
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 16px;
  color: $white-color;
  align-self: flex-start;

  margin-left: $margin-left-header;
  @media (max-width: 450px) {
    font-size: 20px;
  }
}
// }
button {
  height: $button-height;
  width: $button-width;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  border-radius: 10px;
  border: $white-color 1px solid;
  background-color: $main-color;
  color: $white-color;
  transition: $transition;
  align-self: center;
  cursor: pointer;
  &:hover {
    background-color: $background-color;
    border: $background-color 1px solid;
  }

  img {
    width: 30px;
    margin-left: 10px;
  }
  @media (max-width: 450px) {
    width: 90%;
  }
  .github_logo {
    filter: invert(100%);
  }
}
